export default {
	data() {
		return {
			Contactpersons: [],
			Contactperson: {}, //默认联系人
			BUY: [], //商品
			GoodsInfos: [], //提交商品集合
			CustomerId: '',
			disabled: false,
			asd: 0,
		};
	},
	onBackPress() {
		uni.navigateTo({
			url: "./ProductDetails",
		});
		return true
	},
	mounted() {


		this.BUY = this.$store.state.Buy //选择的商品集合
		//切换联系人
		if (this.$store.state.Contactperson.Name) {
			this.Contactperson = this.$store.state.Contactperson
			this.$store.state.Contactperson = {}
			return false
		}
		//获取联系人
		var obj = {
			url: this.$store.state.url + 'CRM/GetCusLinkManMini',
			method: 'GET',
			header: this.$store.state.token,
			data: {
				cusId: this.$store.state.CusId
			}
		}
		this.$http(obj).then((res) => {
			this.Contactpersons = res.Data.Dtos
			console.log(this.Contactpersons)
			for (var i = 0; i < this.Contactpersons.length; i++) {
				if (this.Contactpersons[i].Default) {
					this.Contactperson = this.Contactpersons[i]
					console.log(this.Contactperson)
					return false
				}
			}
			if (!this.Contactperson) {
				console.log("没有默认联系人")
				this.Contactperson = this.Contactpersons[0]
			}
		})
	},
	methods: {
		//付款

		fukuan: function() {
			if (this.asd == 1) {
				return false
			}
			this.asd = 1
			this.disabled = true

			for (var i = 0; i < this.BUY.length; i++) { //输出需要的商品信息集合
				console.log(this.BUY[i].ProductId)
				var GOOD = {}
				GOOD.ProductId = this.BUY[i].ProductId
				GOOD.Number = this.BUY[i].Number
				GOOD.IsAssembly = this.BUY[i].IsAssembly
				this.GoodsInfos[i] = GOOD
			}

			var obj = {
				url: this.$store.state.url + 'CRM/SubmitOrder',
				method: 'POST',
				header: this.$store.state.token,
				data: {
					AddressId: this.Contactperson.LinkManId, //收货地址id
					Type: 2, //1：传工单Id/// 2：传客户Id
					CustomerId: this.$store.state.CustomerId, //客户id
					GoodsInfos: this.GoodsInfos, //商品集合
				}
			}
			this.$http(obj).then((res) => {
				uni.switchTab({
					url:'../mgou'
				})
			})


		},
		//切换联系人
		huan: function() {
			uni.navigateTo({
				url: "./qielxr",
			});
		}
	}
}
